*{font-family:'Roboto', sans-serif}* ::-webkit-scrollbar{width:10px;height:10px}* ::-webkit-scrollbar-track{background:transparent}* ::-webkit-scrollbar-thumb{border-radius:5px;background:#09D3AC}* ::-webkit-scrollbar-thumb:hover{cursor:pointer;background:#09AB8C}html,body{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;overflow-x:hidden !important}body{margin-top:100px;padding-top:20px}.container{display:flex;flex-direction:column;align-items:center}.container .upper_section{width:100%;height:auto;position:relative;display:flex;flex-direction:row;align-items:center;justify-content:flex-start}.container .bottom_section{margin:20px 0 20px 0;width:100%;display:flex;flex-direction:row;align-items:flex-start;justify-content:space-between;height:auto}.container .bottom_section ::-webkit-scrollbar{width:5px;height:5px}.container .bottom_section .left_part{display:flex;flex-direction:row;align-items:center;justify-content:center;padding-right:10px;width:30%;min-height:300px;height:450px}.container .bottom_section .left_part ::-webkit-scrollbar-track{margin:20px 0}@media (max-width: 991px){.container .bottom_section .left_part{height:500px;width:40%}}@media (max-width: 575px){.container .bottom_section .left_part{min-height:230px;height:230px !important;width:100%;padding:0;margin-bottom:20px}}.container .bottom_section .right_part{display:flex;flex-direction:column;align-items:center;justify-content:space-between;padding-left:10px;width:70%;height:450px}@media (max-width: 991px){.container .bottom_section .right_part{height:500px;width:60%}}@media (max-width: 575px){.container .bottom_section .right_part{width:100%;padding:0;max-height:440px;height:auto}}@media (max-width: 575px){.container .bottom_section{flex-direction:column !important}}.bottom_moved{margin-top:0 !important}@media (max-width: 767px){.bottom_moved{margin-top:80px !important}}.moved{height:440px !important}

header{position:fixed;top:0;left:0;z-index:100;margin-bottom:20px;width:100%;height:80px;box-shadow:0 0 20px rgba(0,0,0,0.4);background-color:#222222}header .container{height:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-between}header .container .logo{font-size:30px}header .container .buttons button{outline:none;background:none;border:none;color:#F3F3F3;font-size:20px;padding:0;margin:0 30px;transition:all 0.2s}header .container .buttons button:hover{color:#09D3AC}header .container .buttons button:active{opacity:0.05}header .container .buttons button:first-child{margin-left:0}header .container .buttons button:last-child{margin-right:0}header .container .menu .menu_overlay{position:absolute;top:0;left:0;width:100vw;height:100vh;background-color:rgba(0,0,0,0.5);display:none;z-index:99}header .container .menu .menu_overlay.visible{display:block}header .container .menu .menu_button{display:block;position:relative;z-index:101;transition:all 0.2s cubic-bezier(0.77, 0.2, 0.05, 1)}header .container .menu .menu_button .menu_button_bar{display:block;width:30px;height:2px;border-radius:3px;margin:6px 0 6px 0;transition:all 0.3s ease-in-out;background:#F3F3F3}header .container .menu .menu_button .menu_button_bar:nth-of-type(1){-webkit-transform:rotate(45deg);transform:rotate(45deg);-webkit-transform-origin:10% 10%;transform-origin:10% 10%}header .container .menu .menu_button .menu_button_bar:nth-of-type(2){opacity:0;filter:alpha(opacity=0)}header .container .menu .menu_button .menu_button_bar:nth-of-type(3){-webkit-transform:rotate(-45deg);transform:rotate(-45deg);-webkit-transform-origin:10% 90%;transform-origin:10% 90%}header .container .menu .menu_button.closed .menu_button_bar:nth-of-type(1){-webkit-transform:rotate(0);transform:rotate(0)}header .container .menu .menu_button.closed .menu_button_bar:nth-of-type(2){opacity:1;filter:alpha(opacity=100)}header .container .menu .menu_button.closed .menu_button_bar:nth-of-type(3){-webkit-transform:rotate(0);transform:rotate(0)}header .container .menu .side_menu{height:100vh;display:flex;flex-direction:column;align-items:flex-start;justify-content:space-between;position:absolute;top:0;right:0;width:300px;padding:50px;background:#303030;z-index:100;-webkit-font-smoothing:antialiased;-webkit-transform-origin:0% 0%;transform-origin:0% 0%;-webkit-transform:translate(100%, 0);transform:translate(100%, 0);transition:-webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);transition:transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);transition:transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1)}header .container .menu .side_menu .menu_list{list-style-type:none;margin:50px 0 0 0;padding:0}header .container .menu .side_menu .menu_list li{padding:10px 0;font-size:22px;transition:all 0.2s cubic-bezier(0.77, 0.2, 0.05, 1);cursor:pointer;font-weight:300;color:#09d3ac}header .container .menu .side_menu .menu_list li:hover{padding-left:20px}@media (max-width: 424px){header .container .menu .side_menu{width:230px;padding:30px}}header .container .menu .side_menu.open{-webkit-transform-origin:0% 0%;transform-origin:0% 0%;-webkit-transform:translate(0%, 0%);transform:translate(0%, 0%);box-shadow:0 0 20px #000}

