.planet_data{display:flex;flex-direction:row;justify-content:center;align-items:center;width:100%;padding:0;margin:0 0 0 80px;border-radius:20px !important;transition:all 0.2s}.planet_data:hover{box-shadow:0 0 20px rgba(0,0,0,0.4)}@media (max-width: 767px){.planet_data{margin-left:0;margin-top:80px}}

@-webkit-keyframes spinner{0%{top:92px;left:92px;width:0;height:0;opacity:1}100%{top:16px;left:16px;width:152px;height:152px;opacity:0}}@keyframes spinner{0%{top:92px;left:92px;width:0;height:0;opacity:1}100%{top:16px;left:16px;width:152px;height:152px;opacity:0}}.spinner div{position:absolute;border-width:8px;border-style:solid;opacity:1;border-radius:50%;-webkit-animation:spinner 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:spinner 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite}.spinner div:nth-child(1){border-color:#09d3ac}.spinner div:nth-child(2){border-color:#ffffff;-webkit-animation-delay:-0.7s;animation-delay:-0.7s}.loading_spinner{width:200px;height:200px;display:inline-block;overflow:hidden;background:transparent}.spinner{width:100%;height:100%;position:relative;-webkit-transform:translateZ(0) scale(1);transform:translateZ(0) scale(1);-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-transform-origin:0 0;transform-origin:0 0}.spinner div{box-sizing:content-box}

.planet_content{width:100%;height:100%;display:flex;flex-direction:row;align-items:center !important;justify-content:space-between}.planet_content .planet_image{margin:35px 40px;min-width:200px;width:200px;height:200px;border-radius:20px;transition:all 0.2s;object-fit:cover}.planet_content .planet_image:hover{-webkit-transform:scale(1.1);transform:scale(1.1);box-shadow:0 0 20px rgba(0,0,0,0.4)}@media (max-width: 1199px){.planet_content .planet_image{margin:30px 35px}}@media (max-width: 767px){.planet_content .planet_image{margin:0 20px}}.planet_content .planet_description{padding-right:40px;height:inherit;cursor:default;display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:100%}.planet_content .planet_description h2{margin:0}.planet_content .planet_description ul{margin:0}.planet_content .planet_description ul li{font-size:18px}@media (max-width: 991px){.planet_content .planet_description{padding-right:0;flex-direction:column}.planet_content .planet_description h2{margin:30px 0 20px 0}.planet_content .planet_description ul{margin-bottom:30px}}@media (max-width: 767px){.planet_content .planet_description{padding-right:20px}.planet_content .planet_description h2{margin:20px 0}.planet_content .planet_description ul{margin-bottom:20px}}.planet_content .term{color:#09D3AC}@media (max-width: 575px){.planet_content{flex-direction:column}.planet_content .planet_image{margin-top:20px}}

.error_message{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:30px;color:#09d3ac}.error_message span{font-size:20px}.error_message img{width:100px;height:100px}

.item_list{overflow-x:hidden;width:100%;height:100%;transition:all 0.2s;border-radius:20px}.item_list li{border-left:none;border-right:none;display:flex;align-items:center;width:100%;min-height:45px;font-size:16px;max-width:400px;cursor:pointer;padding:0;transition:all 0.2s}.item_list li:hover{background:#2E2E2E;box-shadow:0 0 20px rgba(0,0,0,0.4);color:#09D3AC}.item_list li:first-child{border-top:none;border-top-right-radius:20px;border-top-left-radius:20px}.item_list li:last-child{border-bottom:none;border-bottom-right-radius:20px;border-bottom-left-radius:20px}.item_list li span{width:100%;padding:10.5px 20px 10.5px 30px;transition:all 0.2s}.item_list li span:hover{padding-left:40px}.item_list li span:active{opacity:0.05}@media (max-width: 991px){.item_list li span:hover{padding-left:40px;padding-right:10px}}@media (max-width: 575px){.item_list li{width:100%;max-width:100%}}@media (max-width: 575px){.item_list{height:230px !important;width:100%}}.list_moved{height:360px;margin-top:80px}@media (max-width: 767px){.list_moved{margin-top:0;height:100%}}

